import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'

// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

on('click', '.js-sub-dependencies', async function ({currentTarget}) {
  const dependency = currentTarget
  const loaded = dependency.getAttribute('data-loaded')
  const expand = dependency.querySelector('.js-expanded')
  const collapse = dependency.querySelector('.js-collapsed')
  const container = dependency.closest('.js-details-container')

  /* eslint-disable-next-line github/no-d-none */
  if (expand) expand.classList.toggle('d-none')
  /* eslint-disable-next-line github/no-d-none */
  if (collapse) collapse.classList.toggle('d-none')
  if (container) container.classList.toggle('color-bg-subtle')

  if (!loaded) {
    dependency.setAttribute('data-loaded', 'true')
    const subDependenciesUrl = dependency.getAttribute('data-sub-dependency-url')
    if (!subDependenciesUrl) throw new Error('failed to get sub-dependency url')
    const parentDependency = dependency.closest('.js-dependency')

    const html = await fetchSafeDocumentFragment(document, subDependenciesUrl)
    if (parentDependency) parentDependency.after(html)
  }
})
